.resume{
  z-index: 20;
  position: relative;
  line-height: 2;

   ul{

     & + ul{
       margin: 1em 0 0;
     }

     & + button {
       margin: 2em 0 0;
     }
   }

  .title{
    font-weight: 900;
    text-transform: uppercase;
    font-size: 1.5em;
    letter-spacing: -.015em;
  }

  .pane{
    display: none;
    padding: 17.5vw 5vw;
    margin: -3.5vw 0;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;

    > div {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
    }

    &:first-child{
      padding: 25vw 5vw 17.5vw;

      &:not(.current){
        padding: 25vw 5vw 20vw;
      }
    }

    &:last-child{
      padding: 20vw 5vw;
    }

    &:first-child,
    &:last-child{
      font-size: 5vw;

      p{
        opacity: .75;
        padding: .5em .75em;
      }

      > div{
        align-items: center;
      }
    }

    &:nth-child(even){
      background: $cyan;
      transform: skewY(-2deg);

      > div{
        transform: skewY(2deg);
      }
    }

    &:nth-child(odd){
      background: $blue;
      transform: skewY(2deg);

      > div{
        transform: skewY(-2deg);
      }
    }

    &.visible{
      display: block;
      transition: .25s all ease-out;
    }

    &:not(.current):not(:first-child){
      padding: 17.5vw 5vw 22.5vw;
    }
  }
}

@include for-tablet-portrait-up{

  .resume {

    .title{
      font-size: 4.5vw;
    }

    .pane {
      padding: 12.5vw 10vw;
      margin: -3.5vw 0;

      &:first-child,
      &:last-child {
        font-size: 3.5vw;
      }

      &:not(.current):not(:first-child){
        padding: 12.5vw 10vw 17.5vw;
      }
    }
  }
}

@include for-tablet-landscape-up{

  .resume{

    .title{
      font-size: 2.5vw;
    }

    .pane{
      padding: 7.5vw 22.5vw;
      margin: -4vw 0;

      &:first-child,
      &:last-child {
        font-size: 2.25vw;

        > div{
          flex-direction: row;
        }
      }

      &:not(.current):not(:first-child){
        padding: 7.5vw 22.5vw 12.5vw;
      }
    }
  }
}
