@mixin container{
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.container {
  @include container;
}

@mixin link($background-color: $black, $underline-color: $white){
  font-weight: 600;
  text-decoration: none;
  text-shadow: -0.08em -0.04em 0 $background-color, 0.08em -0.04em 0 $background-color, 0.08em 0.04em 0 $background-color, -0.08em 0.04em 0 $background-color;
  box-shadow: inset 0 -.06em 0 0 $background-color, inset 0 -.165em 0 0 $underline-color;
}

.link{
  @include link;
}

@mixin button($base-color: $white, $active-text-color: $blue){
  font-weight: 600;
  display: inline-block;
  padding: .5em .75em;
  margin: 0;
  color: $base-color;
  border: .15em solid $base-color;
  transition: $out-transition;
  text-decoration: none;
  position: relative;
  cursor: pointer;
  line-height: 2;
  background: transparent;
  border-radius: 0;
  font-size: 1em;

  &:hover {
    color: $active-text-color;
    background: $base-color;
    transition: $in-transition;
  }
}

button,
a.button {
  @include button;
}
