.freelance{
  display: none;
}

@include for-tablet-portrait-up{

  .freelance{
    width: 100%;
    display: flex;
    position: relative;
    transform: skewY(-2deg);
    z-index: 30;

    .content{
      position: relative;
      background: $cyan;
      width: 100%;
      height: 100%;
      margin: -1.5vw 0 -2vw;
      padding: 4vw 1.5vw;

      &:before,
      &:after{
        content: '';
        width: 100%;
        height: 50%;
        z-index: -1;
        position: absolute;
        transition: $out-transition;
      }

      &:before{
        top: 1px;
        left: 0;
        background: $blue;
      }

      &:after{
        bottom: 1px;
        right: 0;
        background: $violet;
      }

      &.hover{

        &:before,
        &:after{
          transition: $out-transition;
          transform: skewY(3deg);
        }
      }

      p{
        font-size: 2.5vw;
        width: 100%;
        color: $white;
        text-align: center;
        margin: 0 auto;
        transform: skewY(2deg);

        a.button{
          margin: 0 .5em;
        }
      }
    }
  }
}

@include for-tablet-landscape-up{

  .freelance{

    .content{
      margin: -3vw 0 -4vw;

      p{
        font-size: 2vw;
      }
    }
  }
}
